body {
  margin: 0;
  font-family: 'Arial', sans-serif; /* Sets a clean font for the entire app */
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background: linear-gradient(to bottom right, #0d0d0d, #1a1a1a); /* Darker blackish grey gradient */
  min-height: 100vh; /* Full height to cover the viewport */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white; /* White text color for better contrast */
  padding: 20px; /* Add some padding */
}

h1 {
  font-size: 3rem; /* Larger font size for the heading */
  margin-bottom: 20px; /* Space below the heading */
}

.App-link {
  color: #61dafb; /* Link color */
  text-decoration: none; /* Remove underline from links */
}

.App-link:hover {
  text-decoration: underline; /* Underline on hover for better UX */
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
